
























































































































































































import Vue from 'vue';
import { mapActions, mapGetters, mapMutations, mapState } from 'vuex';
import Dialog from './Dialog.vue';
import { fromInteger2Float } from '@/libs';

import Constants from '@/constants';

export default Vue.extend({
  name: 'StakingComponent',
  components: {
    Dialog,
  },
  data: () => ({
    interval: {} as any,
    dialog: false,
    behavior: 'deposit',
    stakable: false,
    token: '',
    errMessage: '',
    profitWithdrawable: false,
    totalStaked: '0',
  }),
  computed: {
    ...mapGetters('staking', ['getProfit', 'getStaked']),
    ...mapState('staking', ['countdown', 'startBlock']),
    ...mapState('erc20', ['blockNumber']),
    ...mapState('wallet', ['chainId']),
  },
  methods: {
    ...mapActions('staking', [
      'calcCountdown',
      'withdrawVNLAProfit',
      'queryVNLAProfit',
      'queryVNLABalance',
      'getStartBlock',
      'getVNLAMinimalStaking',
    ]),
    ...mapActions('erc20', ['balanceOf', 'getBlockNumber']),
    ...mapMutations('staking', ['updateDuration']),
    stake(token: string) {
      this.token = token;
      this.behavior = 'deposit';
      this.dialog = !this.dialog;
    },
    withdraw(token: string) {
      this.token = token;
      this.behavior = 'withdraw';
      this.dialog = !this.dialog;
    },
    async getTotalStaked() {
      this.totalStaked = fromInteger2Float(await this.balanceOf({
        contractAddress: Constants[this.chainId]['VNLA'].address,
        accountAddress: Constants['staking'].address,
      }), 18);
    },
  },
  async created() {
    await this.getBlockNumber();
    console.log(this.blockNumber, this.startBlock);
    this.interval = setInterval(() => {
      this.updateDuration();
      this.queryVNLAProfit();
    }, 1000);
    await this.getStartBlock();
    await this.getVNLAMinimalStaking();
    await this.queryVNLABalance();
    this.calcCountdown();
    this.getTotalStaked().then(console.log, console.error);
  },
  beforeDestroy() {
    if (this.interval) clearInterval(this.interval);
  },
});
