








































































































import Vue from 'vue';
import constants from '@/constants';
import i18n from '@/locale';
import { mapActions, mapGetters, mapState } from 'vuex';
import { fromInteger2Float } from '@/libs';

export default Vue.extend({
  name: 'StakingDialog',
  data: () => ({
    show: false,
    working: false,
    balance: '0',
    tokenAddress: '',
    amount: '',
    needApprove: false,
    errorNotification: false,
    errorMessage: '',
    rules: [
      (val: string) => {
        if (!val) return true;
        if (
          parseFloat(val) === 0 ||
          val === '' ||
          val.match(constants.positiveFloat) ||
          val.match(constants.positiveInteger)
        ) {
          return true;
        } else {
          return i18n.t('StakingInputErr');
        }
      },
    ],
  }),
  computed: {
    ...mapState('wallet', ['chainId', 'accounts']),
    ...mapState('staking', ['decimal', 'staked']),
    ...mapGetters('staking', ['getStaked']),
  },
  methods: {
    ...mapActions('erc20', [
      'balanceOf',
      'convertNumber',
      'allowance',
      'approve',
    ]),
    ...mapActions('staking', [
      'stakeVNLA',
      'withdrawVNLA',
      'queryVNLAProfit',
      'queryVNLABalance',
    ]),
    async loadDialog() {
      if (this.token) {
        this.tokenAddress = constants[this.chainId][this.token].address;
        console.log('Dialog Load', this.tokenAddress, this.decimal);
        if (this.behavior === 'deposit') {
          const value = (
            await this.balanceOf({
              contractAddress: this.tokenAddress,
              accountAddress: this.accounts[0],
            })
          ).toString();
          this.balance = await this.convertNumber({
            value,
            decimals: this.decimal,
          });
        }
        if (this.behavior === 'withdraw') {
          await this.queryVNLABalance();
          this.balance = this.staked;
        }
      }
    },
    async exec() {
      if (this.behavior === 'deposit') {
        try {
          const tx = await this.stakeVNLA(this.amount);
          this.working = true;
          await tx.wait();
          this.working = false;
          this.show = false;
        } catch (error) {
          this.working = false;
          await this.popupErrNotification(error.data.message);
        }
      }
      if (this.behavior === 'withdraw') {
        try {
          const tx = await this.withdrawVNLA(this.amount);
          this.working = true;
          await tx.wait();
          this.working = false;
          this.show = false;
        } catch (error) {
          this.working = false;
          await this.popupErrNotification(error.data.message);
        }
      }
      this.queryVNLAProfit();
      this.queryVNLABalance();
    },
    async isAllowanceEnough() {
      try {
        if (this.token) {
          console.log('query allowance');
          const allowance = parseFloat(
            fromInteger2Float(
              (
                await this.allowance({
                  contractAddress: constants[this.chainId][this.token].address,
                  ownerAddress: this.accounts[0],
                  spenderAddress: constants['staking']['address'],
                })
              ).toString(),
              this.decimal,
            ),
          );
          const balance = parseFloat(this.balance);
          console.log('allowance: ', allowance);
          console.log('balance: ', balance);

          if (allowance < balance) this.needApprove = true;
        }
      } catch (error) {
        this.popupErrNotification(error.data.message);
        console.error(error);
      }
    },
    async getApprove() {
      try {
        const tx = await this.approve({
          contractAddress: constants[this.chainId][this.token].address,
          spenderAddress: constants['staking']['address'],
          amount: constants['allowance'],
        });
        this.working = true;
        await tx.wait();
        this.working = false;
        this.needApprove = false;
      } catch (error) {
        this.working = false;
        this.popupErrNotification(error.data.message);
        console.error(error);
      }
    },
    async popupErrNotification(message: string) {
      this.errorNotification = true;
      this.errorMessage = message;
      setTimeout(() => {
        this.errorNotification = false;
      }, 20000);
    },
  },
  watch: {
    // v-model binding
    async value(val: boolean) {
      await this.loadDialog();
      await this.isAllowanceEnough();
      this.show = val;
    },
    show(val: boolean) {
      this.$emit('input', val);
    },
  },
  props: {
    behavior: {
      type: String,
      default: 'deposit',
      required: true,
    },
    value: {
      type: Boolean,
      default: false,
      required: true,
    },
    token: {
      type: String,
      default: 'VNLA',
      required: true,
    },
  },
});
